.footer {
	position: relative;
	overflow: hidden;

	.container {
		display: flex;

		@include sm {
			padding: 0 20px; } }

	&__logo {
		margin: auto auto auto 0;

		@include sm {
			margin: 10px 0 10px; } }

	&__nav {
		@include md {
			margin-left: 0; }

		@include sm {
			width: 100%;
			margin: 0; } }

	&__socials {
		margin: 0 0 auto auto;

		@include sm {
			margin: 0 0 30px;
			order: 0; }

		.social {
			display: flex;
			gap: 10px;
			margin: 0 auto;
			justify-content: center;
			flex-wrap: wrap;

			@include sm {
				gap: 2px; }

			&.social--theme {
				.social {
					&__item {
						margin: 0; } } }

			&__item {
				margin: 0; }

			&__link {
				width: 80px;
				height: 80px;
				border: 1px solid $themeColor2;
				display: flex;
				justify-content: center;
				align-items: center;
				border-radius: 50%;
				transition: all ease 0.1s;
				background: $mainBg;
				@include sm {
					width: 60px;
					height: 60px; }
				&:hover, &:focus-visible {
					background: $themeColor2;

					.icon {
						fill: $black; } }

				.icon {
					font-size: 24px;
					fill: $themeColor2;
					@include sm {
						font-size: 20px; } } } } }

	&__main {
		padding: clamp(30px, 5vw, 100px) 0;
		display: flex;
		width: 100%;
		flex-direction: column; }

	&__title {
		font-size: clamp(42px, 6vw, 82px);
		font-weight: 800;
		@include black;
		margin-bottom: clamp(26px, 4vw, 40px);
		line-height: 1.06;
		color: $white;

		@include sm {
			text-align: center; } }

	&__mid {
		display: flex;

		@include sm {
			flex-direction: column-reverse;
			align-items: center; } }

	&__basement {

		margin: 0 auto;
		background-position: top center;
		border-top: 1px solid #4E4E4E;
		padding: 40px 0 40px;
		display: flex;

		@include md {
			padding: 30px 0; }

		.container {
			justify-content: space-between;
			gap: 20px;

			@include sm {
				flex-direction: column-reverse; } } }

	&__tip {
		line-height: 1.6;
		font-size: 14px;
		color: #FFF;

		a {
			display: block;
			margin: 0;
			color: #FE5D02; }

		@include sm {
			font-size: 12px; } } }

.f-logo {
	color: $white;
	text-decoration: none;
	display: flex;
	align-items: center;
	gap: 12px;

	@include sm {
		margin: 0 auto;
		width: max-content; }

	&__label {
		font-weight: 500;
		display: flex;
		flex-direction: column;
		flex-wrap: wrap;
		font-size: 12px;
		margin-top: 4px;

		a {
			font-size: 23px;
			@include medium;
			color: $white;
			text-decoration: none; } }

	&__icon {
		width: 50px;

		img {
			width: 100%; } } }

.f-nav {
	display: flex;
	flex-direction: column;
	font-size: 16px;
	font-weight: 500;

	@include sm {
		font-size: 14px;
		text-align: center; }

	&__item {
		&:not(:first-child) {
			margin-top: 7px; } }

	&__link {
		color: $white;

		&:hover {
			text-decoration: none; } } }
